import SearchHotspot from '@/components/SearchHotspot'

export default {
  data () {
    return {
      activeIndex: 0,
      // 分类数据
      categories: [],
      isInit: false,
      toView: 'item1'
    }
  },
  onPageScroll () {
    console.log('scroll')
  },
  components: {
    SearchHotspot
  },
  onLoad () {
    this.getCategories()
  },
  methods: {
    scollHandler () {

    },
    // 请求轮播图数据
    getCategories () {
      this.$request({
        url: '/api/public/v1/categories'
      }).then(data => {
        // console.log(data)
        this.categories = data
        this.isInit = true
      })
    },
    // 跳转到list页面
    toList (name) {
      wx.navigateTo({ url: '/pages/list/main?keyword=' + name })
    }
  }
}
